
.mark-load {
  position: fixed;
  height: 100%;
  width: 100%;
  background: rgba(255, 255, 255, 0.5);
  top: 0;
  left: 0;
}
.mark-load .mark-load__icon {
  color: #388bff;
  position: absolute;
  left: 50%;
  top: 50%;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.mark-load .mark-load__msg {
  color: #666;
  font-size: 1.3rem;
  text-align: center;
  margin-top: 0.8rem;
}



