html,
body,
#app {
  height: 100%;
}
body {
  background: #f1f1f1;
}
#wrapper {
  padding-top: .88rem;
}
.u-icon {
  display: inline-block;
}
.u-icon-dev {
  background: url(../../assets/img/remind@3x.png);
  width: .75rem;
  height: .75rem;
  background-size: cover;
}
.search__icon {
  position: absolute;
  left: auto;
  top: auto;
  width: .44rem;
  height: .44rem;
  background: url(../../assets/img/s01@2x.png) no-repeat;
  background-size: .44rem .44rem;
}
.arrow__icon {
  position: absolute;
  left: auto;
  top: auto;
  width: .44rem;
  height: .44rem;
  background: url(../../assets/img/arrow@2x.png) no-repeat;
  background-size: .44rem .44rem;
}
.position__icon {
  width: .44rem;
  height: .44rem;
  position: absolute;
  left: auto;
  top: auto;
  background: url(../../assets/img/ad@2x.png) no-repeat;
  background-size: .44rem .44rem;
}
.phone__icon {
  position: absolute;
  top: auto;
  left: auto;
  width: .44rem;
  height: .44rem;
  background: url(../../assets/img/phone@2x.png) no-repeat;
  background-size: .44rem .44rem;
  background-color: #fff;
}
.yes__icon {
  position: absolute;
  top: auto;
  left: auto;
  width: .3rem;
  height: .3rem;
  background: url(../../assets/img/personcenter/yes@2x.png) no-repeat;
  background-size: .3rem .3rem;
  background-color: #fff;
}
.borderB {
  border-bottom: 1px solid #ddd;
}
.borderR {
  border-right: 1px solid #ddd;
}
.borderT {
  border-top: 1px solid #ddd;
}
.clearfix::after {
  display: block;
  clear: both;
  height: 0;
  content: "";
  visibility: hidden;
  overflow: hidden;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.tac {
  text-align: center;
}
.activeColor {
  color: #2cc482;
}
.redColor {
  color: #f3515c !important;
}
.disableColor {
  color: #c3c3c3;
}
.activebgColor {
  background-color: #2cc482;
}
.redbgColor {
  background-color: #f3515c;
}
.disablebgColor {
  background-color: #c3c3c3;
}
.wrongColor {
  color: #f3515c !important;
}
.mint-msgbox-btns button {
  font-size: .34rem;
}
.mint-msgbox-input input {
  height: .66rem;
}



